@font-face {
  font-family: 'Pretendard-Medium';
  src: url('../fonts/Pretendard-Medium.ttf') format('truetype');
}

@font-face {
  font-family: 'Pretendard-Bold';
  src: url('../fonts/Pretendard-Bold.ttf') format('truetype');
}


.id-container{
  width: auto;
  font-family: Pretendard-Medium;
  margin-bottom: 200px;
  min-height: 700px;
}
.center-input{
  margin-top: 50px;
  align-items: center;
  text-align: center;
  margin-left: 20px;
}

.center-input input{
  display: block;
  margin: 0 auto;
  margin-bottom: 10px;
  padding: 15px;
  box-sizing: border-box;
  width: 400px;
  border-radius: 5px;
  border-color: lightgray;
}

.id-container button {
  display: block;
  margin: 0 auto;
  margin-bottom: 30px;
  padding: 20px;
  box-sizing: border-box;
  width: 400px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 5px;
  margin-top: 30px;
  font-family: Pretendard-Medium;
  font-size: 20px;
}
.id-container p {
  margin-right: 70px;
  font-family: Pretendard-Medium;
  font-size: 17px;
}
.center-input p {
  margin-right: 80px;
  font-family: Pretendard-Medium;
  font-size: 16px;
}
.user-container{
  margin-top: 20px;
    text-align: center;
    font-family: Pretendard-Bold;
    min-height: 700px;
}
.user-container span{
    margin-right: 15px;
    font-size: 20px;
}
.id-container p {
  margin-left: 50px;
  color: red;
  }
/* 비밀번호 입력 필드에 대한 스타일 */


