}

.center-input span {
margin-left: 50px;
color: red;
}

.user-container{
margin-bottom: 20px;
}

.join-container {
  font-family: Pretendard-Medium;
  min-height: 700px;
}
.join-container button{
  background-color: black;
  border: none;
  border-radius: 8px;
  color: white;
  padding: 20px;
  width: 400px;
  margin-bottom: 30px;
}
.message{
  margin-left: 70px;
}

.find-email span{
  background-color: black;
  color: white;
  padding: 10px;
}

.find-container input {
  width: 480px;
}

.find-container button {
  color: white;
  display: block;
  margin: 0 auto;
  margin-bottom: 30px;
  padding: 20px;
  box-sizing: border-box;
  width: 400px;
  background-color: black;
  border: none;
  border-radius: 5px;
  margin-top: 30px;
  font-family: Pretendard-Medium;
  font-size: 20px;
}
.find-container {
  text-align: center;
  font-family: Pretendard-Medium;
  min-height: 700px;
  padding: 20px;
}


